import React from "react";

import HomePage from "./pages/home_page.js";
import ProjektyPage from "./pages/projekty_page.js";
import ZrucnostiPage from "./pages/zrucnosti_page.js";
import MoviesPage from "./pages/movies_page.js";
import MusicPage from "./pages/music_page.js";
import Todo from "./pages/todo.js"

import Navigation from "./components/navigation.js";
import Footer from "./components/footer.js";
// import "./App.scss";
import {Route} from "react-router-dom";


function App(props) {

    const [theme, changeTheme] = React.useState("theme-light")

    React.useEffect(() => {
        if (document.documentElement.className.includes("nav-open"))
            document.documentElement.className = 'nav-open';
        else
            document.documentElement.className = '';
        document.documentElement.classList.add(`${theme}`);
    })

    const callbackFunction = () => {
        let newtheme = theme === "theme-light" ? "theme-dark" : "theme-light"
        changeTheme(newtheme)
    }

    return (
        <div className="App">

            <Navigation parentCallback={callbackFunction} theme={theme}/>
            <Route path="/todo" render={() => <Todo/>}/>
            <Route exact path="/" render={() => <HomePage/>}/>
            <Route path="/projekty" render={() => <ProjektyPage/>}/>
            <Route path="/zrucnosti" render={() => <ZrucnostiPage/>}/>
            <Route path="/movies" render={() => <MoviesPage/>}/>
            <Route path="/music" render={() => <MusicPage/>}/>
            <Footer/>

        </div>
    );
}

export default App;
