import React from "react";
import Table from "react-bootstrap/Table";
import dataBooks from "../scripts/booksJson.json";

class BooksTableContent extends React.PureComponent {
    render() {
        return <Table striped bordered hover>
            <thead>
            <th/>
            <th>Nzov</th>
            <th>Hodnotenie</th>
            </thead>
            <tbody>
            {dataBooks.bookData.favBooks.sort(() => Math.random() - Math.random()).slice(0, 5).map(book =>
                <tr>
                    <td width="1%">
                        <img src={book.cover}
                             alt="book" height="193" width="128"/>
                    </td>
                    <td>
                        <h5>
                            <a
                                href={book.link}
                                target="_blank"
                            >
                                {book.title} ( {book.year} )
                            </a>
                        </h5>
                        <h7>{book.author}</h7>
                    </td>
                    <td><p>{book.rating}</p></td>
                </tr>
            )}
            </tbody>
        </Table>
    }
}

export default BooksTableContent;
