import React, {useState} from 'react';

const Todo = () => {
    const [todos, setTodos] = useState([
        {text: "Pozak", completed: false},
        {text: "Druha poznamka", completed: true},
        {text: "tretia ", completed: false}
    ])

    const [submitedItem, submitItem] = useState("")

    const handleSubmit = (event) => {
        event.preventDefault();
        const newTodos = [...todos, {text: submitedItem, completed: false}]
        setTodos(newTodos)
    }

    const changeStatus = (index) => {
        const newTodos = [...todos];
        newTodos[index].completed = !newTodos[index].completed;
        setTodos(newTodos);
    }

    const deleteAction = (index) => {
        const newTodos = [...todos];
        newTodos.splice(index, 1);
        setTodos(newTodos);
    }

    const TodoItem = (props) => {

        const handleStatus = () => {
            props.changeStatus(props.id)
        }

        const handleDelete = () => {
            props.deleteAction(props.id)
        }


        return (
            <div style={{width: "80%", display: "flex", justifyContent: "space-evenly"}}>
                <p style={{textDecoration: props.isCompleted ? "line-through" : ""}}>{props.id + 1}. {props.text}</p>
                <button onClick={handleStatus}>{props.isCompleted ? "Uncheck" : "Check"}</button>
                <button onClick={handleDelete}>Delete</button>
            </div>

        )
    }
    return (
        <div id="content">
            <h1>TODO APLIKACIA</h1>
            <form onSubmit={handleSubmit}>
                <p>Pridaj item</p>
                <input autoComplete="off" type="text" name="itemName"
                       onChange={(event) => submitItem(event.target.value)} required/>
                <input type="submit" value="Submit"/>
            </form>

            <ul>
                {todos.map((todo, index) =>
                    <TodoItem
                        text={todo.text}
                        id={index} // ANTI PATTER !!!
                        isCompleted={todo.completed}
                        changeStatus={changeStatus}
                        deleteAction={deleteAction}/>
                )}
            </ul>
        </div>
    );
};

export default Todo;
