import React from "react";
import Table from "react-bootstrap/Table";
import dataShow from "../scripts/showsJson.json";
import dataMovie from "../scripts/movieJson.json";

class MoviesTableContent extends React.PureComponent {
  render() {
    let typeOfData;
    let typeOfLink;
    if (this.props.typeOfData === "movies") {
      typeOfData = dataMovie.movieData.favMovies;
      typeOfLink = "https://www.taste.io/movies/";
    } else {
      typeOfData = dataShow.showData.favShows;
      typeOfLink = "https://www.taste.io/tv/";
    }

    return (
      <Table striped bordered hover>
        <thead>
          <th />
          <th>Nzov</th>
          <th>Hodnotenie</th>
        </thead>
        <tbody>
          {typeOfData
            .sort(() => Math.random() - Math.random())
            .slice(0, 5)
            .map((movie) => (
              <tr>
                <td width="1%">
                  <img
                    src={"https://image.tmdb.org/t/p/w500/" + movie.poster}
                    alt="movie"
                    height="200"
                    width="max"
                  />
                </td>
                <td>
                  <h5>
                    <a href={typeOfLink + movie.slug} target="_blank">
                      {movie.name} ( {movie.year} )
                    </a>
                  </h5>
                </td>
                <td>
                  <p>{movie.rating.toFixed(2)} / 5</p>
                </td>
              </tr>
            ))}
        </tbody>
      </Table>
    );
  }
}

export default MoviesTableContent;
