import React from 'react'

export default function Zrucnosti_page() {

    function stopPulsate(e) {
        e.target.style.cssText = e.target.style.cssText + " transform: scale(1.2); " +
            "filter: grayscale(0%);" +
            "background-color: rgba(0,0,0, 0.18); " +
            "box-shadow: 0 0 120px 0px #000; " + "border-radius: 30%;"
        e.target.className = ""
        e.target.nextSibling.style.display = 'inherit'
    }

    function startPulsate(e) {
        e.target.className = "pulsate-fwd"
        e.target.style.cssText = e.target.style.cssText +
            "transform: scale(1); " +
            "filter: grayscale(100%); " +
            "box-shadow: none;" +
            "background-color: transparent;"
        // "background-color: rgba(0,0,0,  0.18); " +
        // "box-shadow: 0 0 120px 0px #000; "
        e.target.nextSibling.style.display = 'none'
    }

    function CreateDots() {
        let riadok = 3
        let base = 200
        if (window.innerWidth < 900) {
            riadok = 2
        }
        if (window.innerWidth < 501) {
            riadok = 1
            base = 150
        }
        console.log("riadok " + riadok)
        let y = 10;
        let nepar = true;
        let x = 0;
        return <div className={"zrucnost"} style={{position: "relative", height: "500px", marginTop: "50px"}}>
            {
                [["c01.png", "C"], ["html01.png", "HTML"], ["css01.png", "CSS"], ["git.png", "GIT"], ["java01.png", "JAVA"],
                    ["js01.png", "JavaScript"], ["php01.png", "PHP"], ["postgressql01.png", "PostgreSQL"], ["python01.png", "Python"],
                    ["react01.png", "React JS"]
                ].sort(() => Math.random() - Math.random()).map((item, i) => {

                    x = x + base;


                    if (nepar === true && i === riadok) {
                        y = y + 150
                        nepar = false;
                        x = base - (base / 2)
                        //x = x - 100;
                    } else if ((i % (riadok + riadok + 1)) === 0 && nepar === false) {
                        y = y + 150
                        nepar = true;
                        x = base;
                    } else if (i > riadok && ((i) % (riadok)) === 1 && nepar === true && riadok !== 1) {
                        y = y + 150
                        nepar = false;
                        x = base - (base / 2);
                    } else if (i > riadok && riadok === 1 && nepar === true) {
                        y = y + 150
                        nepar = false;
                        x = base - (base / 2);
                    }

                    return <figure style={{position: "absolute", left: x + "px", top: y + "px"}}><img
                        className={"pulsate-fwd"}
                        id={i}
                        onMouseOver={stopPulsate}
                        onMouseLeave={startPulsate}
                        src={require("../../src/assets/img/skills/" + item[0])}
                        height="80"
                        width="80"
                        alt="skill"
                        style={{
                            filter: "grayscale(100%)",
                            // backgroundColor: "rgba(0,0,0, 0.18)",
                            // boxShadow: "0 0 120px 0px #000",
                            borderRadius: "30%"
                        }}
                    />
                        <figcaption
                            style={{
                                textAlign: "center",
                                fontWeight: "bold",
                                display: "none",
                                marginTop: "15px"
                            }}>{item[1]}</figcaption>
                    </figure>
                })
            }
        </div>
    }

    return (
        <main>
            <div id="content">
                <h1>Zrunosti</h1>
                <br/>
                <p>Zoznam jazykov, frameworkov a programov s ktormi som pracoval alebo s ktormi aktulne pracujem.</p>
                <hr/>
                <CreateDots/>
            </div>
        </main>
    )
}
